export default {
	props:{
		showModal: {
			// 要显示的数组
			type: Boolean,
			default() {
				return false;
			}
		}
	},
	data() {
		return {
			text:''
		};
	},
	methods:{
		inputChange(e){
			this.text=e.detail.value
		},
		relaseComment(){
			this.$emit('relaseComment',this.text)
		},
		cancelRelase(){
			this.text=""
			this.$emit('cancelRelase')
		}
	}
}
